import React, { useEffect } from 'react'

const Toolbar = () => {

    const toogleContrast = (init) => {
        if (window.sessionStorage) {
            const contrast = "contrast"
            const current = window.sessionStorage.getItem(contrast)
            window.sessionStorage.setItem(contrast, !init || (init && current === undefined) ? !JSON.parse(current || true) : (current || false))
        }
    }

    const changeFontSize = (size) => {
        if (window.sessionStorage) {
            const fontSize = "font-size"
            const current = window.sessionStorage.getItem(fontSize)

            if (size === 0) {
                window.sessionStorage.setItem(fontSize, (current || 100));
            }
            else {
                window.sessionStorage.setItem(fontSize, +(current || 100) + size);
            }
        }
    }

    useEffect(() => {
        toogleContrast(true)
        changeFontSize(0)
    }, [])

    const [count, setCount] = useState(100);

    return (
        <React.Fragment>
            <div className="app-toolbar" style={{ fontSize: `${count}%` }}>
                <div className="app-toolbar--left">
                    <div className="app-toolbar--item" onClick={() => setCount(10)}>
                        <span>A+</span>
                    </div>
                    <div className="app-toolbar--item" onClick={() => setCount(-10)}>
                        <span>A-</span>
                    </div>
                    <div className="app-toolbar--item" onClick={() => toogleContrast(false)}>
                        <span>Wersja kontrastowa</span>
                    </div>
                </div>
                <div className="app-toolbar--right">
                    <div className="app-toolbar--item">
                        <span>EN</span>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Toolbar